import s from './ImageGallery.module.css';
import React, { PureComponent } from 'react';
import LoaderPend from "../Loader";
import ImageGalleryItem from '../ImageGalleryItem';
// import Button from './components/Button'
// import Modal from './components/Modal'


export default class ImageGallery extends PureComponent{
state = {
    pictures: [],
    error: null,
    showModal: false,
    status: 'idle',
    page:1,
};


componentDidUpdate(prevProps, prevState) {
    const prevValue = prevProps.queryValue ;
    const nextValue = this.props.queryValue;
    // if(prevValue === nextValue) {
    //     return }
    // else {
        if(prevValue !== nextValue) {
        this.setState({ status: 'pending'})
        const KEY_API = '23041177-0c0b450b7629b324f32016842';
        const BASE_URL = 'https://pixabay.com/api/?key=';
        fetch(`${BASE_URL}${KEY_API}&q=${nextValue}&page=${this.state.page}&per_page=12&image_type=photo`)
        .then(response => {
            if(response.ok) {
               return response.json()
                 } 

               return Promise.reject({
                   error: new Error(`nothing found for the your request ${nextValue}`),
                });     
            }) 
        .then(pictures=> this.setState({pictures: pictures.hits, status: 'resolved' }))
        .catch(error => this.setState({error, status: 'rejected' }))
    }
}


toggleModal(){
    this.setState(state => ({ showModal: !state.showModal }));
}

render(){
    const {pictures, error, status} = this.state;
    // const{} = this.props
    if(status === 'idle') {
       return  <p>Input your query</p>  
    }

    if(status === 'pending') {
        return <LoaderPend/>
    }

    if(status === 'rejected') {
        return <p>Whoops, something went wrong: {error.message}</p>
    }

    if(status === 'resolved') {
        return(
            <ul className={s.ImageGallery}>
                <ImageGalleryItem
                    pictures={pictures}
                    onToggle={this.toggleModal}
                />
            </ul>
            // {showModal && <Modal onClose={this.toggleModal}/>}
        )
    }
    
}
}